import React from 'react';
import {connect, Provider} from "react-redux";
import update from 'immutability-helper';
import {createStore} from 'redux';
import ReactDOM from 'react-dom';
import './index.css';


function reducer(state, action) {
    switch (action.type) {
        case 'ADD_KNOWLEDGE':
            return update(state, {knowledgeClasses: {[action.className]: {$set: {className: action.className}}}});
        case 'DELETE_KNOWLEDGE':
            return update(state, {knowledgeClasses: {$unset: [action.className]}});
        case 'ADD_PERSON':
            return update(state, {people: {[action.personName]: {$set: {personName: action.personName}}}});
        case 'DELETE_PERSON':
            return update(state, {people: {$unset: [action.personName]}});
        case 'LOAD_STATE':
            return JSON.parse(action.state);
        default:
            return state
    }
}

const dispatchToProps = (dispatch) => {
    return {
        addKnowledgeClass: (className) => {
            dispatch({type: "ADD_KNOWLEDGE", className})
        },
        deleteKnowledgeClass: (className) => {
            dispatch({type: "DELETE_KNOWLEDGE", className})
        },
        addPerson: (personName) => {
            dispatch({type: "ADD_PERSON", personName})
        },
        deletePerson: (personName) => {
            dispatch({type: "DELETE_PERSON", personName})
        },
        loadState: (state) => {
            dispatch({type: "LOAD_STATE", state})
        }
    }
};


const initState = {
    knowledgeClasses: {},
    people: {}
};

const store = createStore(reducer, initState, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());

const stateToProps = (s) => {
    return {...s};
};

class KnowledgeClass extends React.Component {

    render() {
        return (
            <div className={"knowledge"}>
                <b>Knowledge name: {this.props.className}</b>
                <br/>
                <b onClick={() => this.props.deleteKnowledgeClass(this.props.className)}>Delete</b>
            </div>
        );
    }
}

class Person extends React.Component {

    render() {
        return (
            <div className={"person"}>
                <b>Person name: {this.props.personName}</b>
                
                <br/>
                <b onClick={() => this.props.deletePerson(this.props.personName)}>Delete</b>
            </div>
        );
    }
}

class Landscape extends React.Component {

    addKnowledge(e, props) {
        e.preventDefault();
        props.addKnowledgeClass(e.target.knowledgeClassName.value);
    }

    addPerson(e, props) {
        e.preventDefault();
        props.addPerson(e.target.personName.value);
    }

    loadState(e) {
        e.preventDefault();
        this.props.loadState(this.stateArea.value);
    }

    render() {
        return (
            <div className={"landscape"}>

                <div className={"knowledge_classes"}>
                    <b>Knowledge classes:</b>
                    {Object.keys(this.props.knowledgeClasses).map((className) => (
                        <KnowledgeClass className={className} deleteKnowledgeClass={this.props.deleteKnowledgeClass}/>
                    ))}
                    <div className={"addKnowledgeContainer"}>
                        <form onSubmit={(e) => this.addKnowledge(e, this.props)}>
                            <label>
                                Add knowledge category:
                                <input type="text" name="knowledgeClassName"/>
                            </label>
                            <input type="submit" value="Add knowledge"/>
                        </form>
                    </div>
                </div>

                <div className={"people"}>
                    <b>People:</b>
                    {Object.keys(this.props.people).map((personName) => (
                        <Person personName={personName} deletePerson={this.props.deletePerson}/>
                    ))}
                    <div className={"addPersonContainer"}>
                        <form onSubmit={(e) => this.addPerson(e, this.props)}>
                            <label>
                                Add person:
                                <input type="text" name="personName"/>
                            </label>
                            <input type="submit" value="Create person"/>
                        </form>
                    </div>
                </div>

                <div className={"state"}>
                    <form onSubmit={(e) => this.loadState(e)}>
                        <textarea ref={(stateArea) => this.stateArea = stateArea}/>
                        <input type="submit" value="Load saved state"/>
                    </form>
                    <code>
                        {JSON.stringify(this.props)}
                    </code>
                </div>
            </div>
        );
    }
}


const ConnectedLandscape = connect(stateToProps, dispatchToProps)(Landscape);


// ========================================

ReactDOM.render(
    <Provider store={store}>
        <ConnectedLandscape/>
    </Provider>,
    document.getElementById('root')
);




